import { useEffect, useState } from "react"
import cities from '../../db.json'
import { findNearest, getDistance } from "geolib";
import './row.css';
import mapLogo from './../../assets/map-logo.png'
import Route from "../Route/route";
import convertTimestamp from "../../utils/convertDate";

function Row({ truck, index, setCoords, setDisplayMap, isToggled, updateDB }) {

    const [distance, setDistance] = useState(0)

    const [closestLoc, setClosestLoc] = useState('')

    const [currentCoords, setCurrentCoords] = useState({})

    const [geo, setGeo] = useState([])

    const [date, setDate] = useState(truck.uploading)

    const [routes, setRoutes] = useState([])

    useEffect(() => {
        if (truck.latitude) {
            if (isToggled) {
                setGeo(cities.filter(city => {
                    if (city.peoples > 25000) {
                        return city
                    } else {
                        return 0
                    }
                }))
            } else {
                setGeo(cities)
            }
            let { latitude, longitude } = truck;
            setCurrentCoords({ latitude, longitude })
        }
        setDate(truck.uploading)
    }, [truck, isToggled])

    const isBase = truck.latitude > 54.857 && truck.latitude < 54.859 && truck.longitude > 56.098 && truck.latitude < 56.0995

    useEffect(() => {
        if (currentCoords) {
            let nearest = findNearest(currentCoords, geo)
            setClosestLoc(nearest)
            if (nearest) {
                let lat = nearest.latitude
                let lon = nearest.longitude
                let distance = getDistance(currentCoords, { lat, lon })
                setDistance(`${~~(distance / 1000)} км`)
            }
        }
    }, [currentCoords, geo, truck])

    const mapHandler = (e) => {
        setCoords([currentCoords.latitude, currentCoords.longitude])
        setDisplayMap(true)
    }

    const dateHandler = (e) => {
        let uploadingDate = Date.parse(e.target.value)
        setDate(Date.parse(e.target.value))
        updateDB(truck.number.substr(2, 3), { routes, uploadingDate })
    }

    return <tr className="row" style={{ backgroundColor: truck.uploading < Date.now() && !isBase ? 'rgba(84, 6, 6, 0.2)' : '' }}>
        <td className="cell">{index + '.'}</td>
        <td className="cell">{truck.driver}</td>
        <td className="cell">{truck.number}</td>
        <td className="cell">
            <Route truck={truck} updateDB={updateDB} routes={routes} setRoutes={setRoutes} />
        </td>
        <td className="cell">
            <input type="date" className="date" value={convertTimestamp(date)} onChange={dateHandler} />
        </td>
        <td className="cell">
            {!isBase ? `${truck.latitude ? closestLoc?.locality_name + ` (${distance})` : ''} ` : 'База'}
        </td>
        <td className="cell">
            {
                truck?.longitude && <img src={mapLogo} alt="" className="cell logo" onClick={mapHandler} />
            }
        </td>

    </tr >
}


export default Row