import { useEffect, useState } from "react";
import API from "../../api";
import Row from "../Row/row";
import trucksDB from "../../trucks.json"
import './table.css';
import Popup from "../Popup/popup";
import ToggleSwitch from "../ToggleSwitch/toggleSwitch";


function Table({ routes, updateDB }) {

    const [fetch, setFetch] = useState(false)

    const [displayMap, setDisplayMap] = useState(false)

    const [coords, setCoords] = useState([55, 55])

    const [isToggled, setIsToggled] = useState(localStorage.getItem('isToggled') ? JSON.parse(localStorage.getItem('isToggled')) : false)

    const [trucks, setTrucks] = useState([])

    const sortNumbersUp = (e) => {
        setTrucks([...trucks.sort((a, b) => a.number.substr(2, 3) - b.number.substr(2, 3))])
    }
    const sortNumbersDown = (e) => {
        setTrucks([...trucks.sort((a, b) => b.number.substr(2, 3) - a.number.substr(2, 3))])
    }
    const sortDriverUp = (e) => {
        setTrucks([...trucks.sort((a, b) => a.driver.localeCompare(b.driver))])
    }
    const sortDriverDown = (e) => {
        setTrucks([...trucks.sort((a, b) => b.driver.localeCompare(a.driver))])
    }
    const sortDateUp = (e) => {
        setTrucks([...trucks.sort((a, b) => a.uploading - b.uploading)])
    }
    const sortDateDown = (e) => {
        setTrucks([...trucks.sort((a, b) => b.uploading - a.uploading)])
    }

    useEffect(() => {
        API.getCoords().then(data => {
            for (let i = 0; i < data.length; i++) {
                trucksDB[i].latitude = data[i].coord.y
                trucksDB[i].longitude = data[i].coord.x
                trucksDB[i].speed = data[i].coord.speed
            }
            return data
        }).then(data => {
            setFetch(true)
        }).finally(() => {
            for (let i = 0; i < trucksDB.length; i++) {
                if (routes) {
                    trucksDB[i].route = routes[trucksDB[i].number.substr(2, 3)].routes
                    trucksDB[i].uploading = routes[trucksDB[i].number.substr(2, 3)].uploadingDate
                }
            }
            setTrucks(trucksDB.sort((a, b) => a.number.substr(2, 3) - b.number.substr(2, 3)))
        })
    }, [routes])

    return <div>
        <table className="table">
            <thead>
                <tr>
                    <th>№</th>
                    <th>
                        <div className="header">
                            Водитель
                            <div>
                                <div className="arrow" onClick={sortDriverUp}>▲</div>
                                <div className="arrow" onClick={sortDriverDown}>▼</div>
                            </div>
                        </div>
                    </th>
                    <th>
                        <div className="header">
                            Номер
                            <div>
                                <div className="arrow" onClick={sortNumbersUp}>▲</div>
                                <div className="arrow" onClick={sortNumbersDown}>▼</div>
                            </div>
                        </div>
                    </th>
                    <th>Маршрут</th>
                    <th>
                        <div className="header">Дата выгрузки
                            <div>
                                <div className="arrow" onClick={sortDateUp}>▲</div>
                                <div className="arrow" onClick={sortDateDown}>▼</div>
                            </div>
                        </div>
                    </th>
                    <th>
                        <div>
                            {`
                                Текущее местоположение на ${('0' + new Date().getHours()).slice(-2)}:${('0' + new Date().getMinutes()).slice(-2)}
                                ${('0' + new Date().getDate()).slice(-2)}.${('0' + (new Date().getMonth() + 1)).slice(-2)}.${new Date().getFullYear()}
                            `}
                        </div>
                        <div className="show-cities">
                            Показывать только крупные города
                            <div className="toggle">
                                <ToggleSwitch isToggled={isToggled} setIsToggled={setIsToggled} />
                            </div>
                        </div>
                    </th>
                    <th>Карта</th>
                </tr>
            </thead>
            <tbody>
                {
                    fetch &&
                    trucks.map((truck, i) => (
                        <Row
                            updateDB={updateDB}
                            isToggled={isToggled}
                            key={i}
                            index={i + 1}
                            truck={truck}
                            setDisplayMap={setDisplayMap}
                            setCoords={setCoords}
                        />
                    ))
                }
            </tbody>
        </table>
        <Popup
            coords={coords}
            displayMap={displayMap}
            setDisplayMap={setDisplayMap}
        />
    </div>
}

export default Table;