import React, { Component } from "react";
import autoKeyboard from "../../../utils/keyboard";
import './autocomplete.css'

class Autocomplete extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activeSuggestion: -1,
            filteredSuggestions: [],
            showSuggestions: false,
            userInput: ""
        };
    }

    onChange = e => {
        const { suggestions } = this.props;
        const userInput = e.currentTarget.value;

        const filteredSuggestions = suggestions.filter(
            suggestion =>
                suggestion.toLowerCase().startsWith(autoKeyboard(userInput.toLowerCase()))
        );

        this.setState({
            activeSuggestion: -1,
            filteredSuggestions,
            showSuggestions: true,
            userInput: e.currentTarget.value
        });
    };

    onClick = e => {
        this.setState({
            activeSuggestion: 0,
            filteredSuggestions: [],
            showSuggestions: false,
            userInput: ''
        });
        this.props.setRoutes(prevState => [...prevState, e.currentTarget.innerText])
        let routes = [...this.props.routes, e.currentTarget.innerText]
        let uploadingDate = this.props.truck.uploading
        this.props.updateDB(this.props.truck.number.substr(2, 3), { routes, uploadingDate })
        this.props.setShowInput(false)
    };

    onFocus = e => {
        this.setState({
            activeSuggestion: -1,
            filteredSuggestions: this.props.suggestions,
            showSuggestions: true,
            userInput: ''
        });
    }

    onBlur = e => {
        this.props.setShowInput(false)
    }

    onKeyDown = e => {
        const { activeSuggestion, filteredSuggestions } = this.state;

        if (e.keyCode === 13) {
            this.setState({
                activeSuggestion: 0,
                showSuggestions: false,
            });
            if (activeSuggestion >= 0 && filteredSuggestions.length) {
                this.props.setRoutes(prevState => [
                    ...prevState,
                    autoKeyboard(this.state.filteredSuggestions[activeSuggestion].charAt(0).toUpperCase() +
                        this.state.filteredSuggestions[activeSuggestion].slice(1).toLowerCase())
                ])

                let routes = autoKeyboard(this.state.filteredSuggestions[activeSuggestion].charAt(0).toUpperCase() +
                    this.state.filteredSuggestions[activeSuggestion].slice(1).toLowerCase())
                let uploadingDate = this.props.truck.updloading
                this.props.updateDB(this.props.truck.number.substr(2, 3), { routes, uploadingDate })

            } else {
                this.props.setRoutes(prevState => [
                    ...prevState,
                    autoKeyboard(e.currentTarget.value.charAt(0).toUpperCase() + e.currentTarget.value.slice(1).toLowerCase())
                ])
                let routes = [...this.props.routes, autoKeyboard(e.currentTarget.value.charAt(0).toUpperCase() + e.currentTarget.value.slice(1).toLowerCase())]
                let uploadingDate = this.props.truck.uploading
                this.props.updateDB(this.props.truck.number.substr(2, 3), { routes, uploadingDate })
            }
            this.props.setShowInput(false)
        }

        else if (e.keyCode === 38) {
            if (activeSuggestion === 0) {
                return
            }
            this.setState({ activeSuggestion: activeSuggestion - 1 });
        }

        else if (e.keyCode === 40) {
            if (activeSuggestion - 1 === filteredSuggestions.length) {
                return;
            }
            this.setState({ activeSuggestion: activeSuggestion + 1 });
        }
    };



    render() {
        const {
            onChange,
            onClick,
            onKeyDown,
            onFocus,
            onBlur,
            state: {
                activeSuggestion,
                filteredSuggestions,
                showSuggestions,
                userInput
            }
        } = this;

        let suggestionsListComponent;

        if (showSuggestions) {
            if (filteredSuggestions.length) {
                suggestionsListComponent = (
                    <ul className="suggestions">
                        {filteredSuggestions.map((suggestion, index) => {
                            let className;
                            if (index === activeSuggestion) {
                                className = "suggestion-active";
                            }

                            return (
                                <li className={className} key={suggestion + index} onClick={onClick}>
                                    {suggestion}
                                </li>
                            );
                        })}
                    </ul>
                );
            } else {
                suggestionsListComponent = (
                    <div className="no-suggestions">
                        <em>Ничего не найдено</em>
                    </div>
                );
            }
        }

        return (
            <div className="autocomplete">
                <input
                    autoFocus
                    className="citiesInput"
                    type="text"
                    onChange={onChange}
                    onKeyDown={onKeyDown}
                    onFocus={onFocus}
                    onBlur={() => setTimeout(() => onBlur(), 180)}
                    value={userInput}
                />
                {suggestionsListComponent}
            </div>
        );
    }
}

export default Autocomplete;
